.home {
  display: flex;
}
.home .homeContainer {
  flex: 6;
  min-height: 100vh;
}
.home .homeContainer .body {
  display: flex;
  justify-content: center !important;
  align-items: center;
  flex: 1;
  min-height: calc(93% - 0.5px);
  margin-top: 20px;
}
.home .homeContainer .formContainer {
  justify-content: center;
  display: wrap;
  align-items: center;
  background-color: white;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  width: 60%;
  padding: 2% 3%;
}
.home .homeContainer .formContainer form {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}
.home .homeContainer .formContainer form .nameblock {
  display: flex;
  justify-content: space-between;
}
.home .homeContainer .formContainer form .formInput {
  width: calc(47% + 4px);
  margin-right: 5px;
}
.home .homeContainer .formContainer form .formInput label {
  display: flex;
  align-items: center;
  gap: 3%;
  margin-bottom: 10px;
}
.home .homeContainer .formContainer form .formInput label .icon {
  cursor: pointer;
}
.home .homeContainer .formContainer form .formInput input {
  width: 100%;
  padding: 8px;
  border: 1px solid gray;
  border-radius: 5px;
}
.home .homeContainer .formContainer form .formInput select, .home .homeContainer .formContainer form .formInput option {
  width: 100%;
  padding: 8px;
  border: 1px solid gray;
  border-radius: 5px;
}
.home .homeContainer .formContainer form button {
  width: 250px;
  padding: 10px;
  border: none;
  background-color: #21becc;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
}h3{
  font-weight: 300;
  font-size: 1.75rem;
  margin: 0;
}
.home .homeContainer .formContainer form .profileImg {
  display: inline;
  width: 100%;
}
.home .homeContainer .formContainer form .addImgText {
  font-weight: 500;
  margin-right: 10px;
}
